.bg-header-block {
  background: url("../img/head_bckg_lite3.jpg") no-repeat center center;
  background-size: cover;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
  height: 176px;
  padding: 2rem;
  position: relative;
  @media (max-width: 767px) {
    height: auto; } }

.slogan {
  color: #063f63;
  font-family: "Arial Narrow";
  font-size: 21px;
  line-height: 24px;
  font-weight: bold; }

.slogal_part2 {
  color: #063f63;
  font-family: "Arial Narrow";
  font-size: 17px;
  font-weight: bold; }

.under_slogan_text {
  color: #000000;
  font-size: 11px;
  line-height: 12px;
  padding-top: 1rem; }

.carousel.slide.header {
  position: absolute;
  top: 0;
  right: 0;
  width: 195px;
  overflow: hidden; }
