// Variables Bootstrap 4

body{
  font-size: 14px;
  line-height: 1.5;
  font-family: 'Arial';
  color: black;
}

a,
.btn-link {
  color: #3d5c7e;
}

$red:     #cc2eaa;
$green:   #2ecc71;

a.active.focus,
a.active:focus,
a.focus,
a:active.focus,
a:active:focus,
a:focus,
button.active.focus,
button.active:focus,
button.focus,
button:active.focus,
button:active:focus,
button:focus,
.btn.active.focus,
.btn.active:focus,
.btn.focus,
.btn:active.focus,
.btn:active:focus,
.btn:focus {
  outline: 0!important;
  outline-color: transparent!important;
  outline-width: 0!important;
  outline-style: none!important;
  box-shadow: 0 0 0 0 rgba(0,123,255,0)!important;
}

